@charset "UTF-8";
/** Media Queries **/
.wc_decripciones {
  margin-top: 3rem;
}
.wc_decripciones h1 {
  font-size: 2.2rem;
  font-weight: 400;
  margin: 0 0 1.5rem 0;
}
.wc_decripciones h1 span {
  font-weight: 900;
}
@media (min-width: 768px) {
  .wc_decripciones h1 {
    font-size: 2.5rem;
  }
}
.wc_decripciones ul li {
  color: #000000;
}
.wc_decripciones p {
  color: #000000;
  text-align: justify;
  line-height: 1.5;
}
.wc_decripciones p:last-of-type {
  margin: 0;
}

.woocommerce span.onsale {
  display: none !important;
}
.woocommerce ul.products {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
.woocommerce ul.products::before {
  display: none !important;
}
.woocommerce ul.products li.product {
  width: 100% !important;
  margin: 0 !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 1rem;
}
.woocommerce ul.products li.product img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  margin-bottom: 0 !important;
}
@media (min-width: 768px) {
  .woocommerce ul.products {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .woocommerce ul.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.woocommerce-breadcrumb {
  margin: 0 0 1rem 0 !important;
}

.woocommerce div.product div.summary {
  display: block;
  width: 100%;
  float: none;
  clear: both;
}

.wc_pagar {
  text-align: center;
}
.wc_pagar__boton {
  background-color: #1D57C5;
  padding: 1.5rem 4rem;
  color: #FFFFFF !important;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_pagar__boton {
    width: auto;
  }
}
.wc_pagar__boton:hover {
  background-color: #164399;
  color: #FFFFFF;
  cursor: pointer;
}

.wc_info {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  color: #000000;
  background-color: #f6d88a;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.wc_titulo {
  display: flex;
  margin-bottom: 1rem;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .wc_titulo {
    height: 7rem;
    overflow: hidden;
  }
}
.wc_titulo__title {
  font-size: 1.6rem;
  font-weight: 500;
  margin: 0 !important;
  text-align: center;
}

.wc_detalles {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.wc_detalles__detalle {
  border-right: 1px solid #cccccc;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
}
.wc_detalles__detalle:last-of-type {
  border: none;
}
.wc_detalles__icono {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #000000;
  margin: 0.4rem 0 !important;
}
.wc_detalles__icono i {
  width: 3rem;
  color: #1D57C5;
  font-size: 1.8rem;
  text-align: center;
}

.wc_etiqueta {
  background-color: #164399;
}
.wc_etiqueta p {
  color: #FFFFFF;
  padding: 0.6rem;
  font-weight: 500;
  text-align: center;
}

.wc_btn_inicio {
  display: flex;
  flex-direction: column;
}
.wc_btn_inicio__boton {
  padding: 1rem;
  background-color: #a31011;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.wc_btn_inicio__boton:hover {
  background-color: #7e0c0d;
  color: #FFFFFF;
}

.wc_header {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  border-bottom: 1px solid #009489;
  margin-bottom: 2rem;
}
.wc_header__descripcion {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.wc_header .wc_datos {
  order: 2;
}
.wc_header .wc_imagen {
  order: 1;
}
@media (min-width: 1024px) {
  .wc_header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .wc_header .wc_datos {
    order: 1;
  }
  .wc_header .wc_imagen {
    order: 2;
  }
}
.wc_header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .wc_header__contenido {
    flex-direction: row;
  }
}
.wc_header__coordinador {
  color: #000000;
  margin: 0;
}
.wc_header__coordinador span {
  font-weight: bold;
}
.wc_header__descargar {
  font-weight: bold;
  font-size: 2rem;
  color: #00615A;
}
.wc_header__descargar:hover {
  color: #002e2b;
}

.wc_planes {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  margin-top: 3rem;
  color: #000000;
}
.wc_planes__simple {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wc_planes__simple {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.wc_planes__cart {
  background-color: #E3E3E3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  border-radius: 1rem;
  overflow: hidden;
}
.wc_planes__head {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  background-color: #b0b0b0;
  font-weight: 600;
  padding: 1rem;
}
.wc_planes__contenido {
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.wc_planes__ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.wc_planes__li {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
  text-align: center;
}
.wc_planes__footer {
  padding: 2rem;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 600;
  background-color: #979797;
}
.wc_planes__pagar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.wc_planes__pagar::before {
  content: "";
  width: 1.5rem;
  height: 1.5rem;
  border: 2px solid #1D57C5;
  border-radius: 50%;
}

.wc_variaciones {
  color: #000000;
  max-width: 50rem;
  margin: 0 auto;
}
.wc_variaciones__h3 {
  font-size: 1.6rem;
  text-align: center;
}
.wc_variaciones__item {
  background-color: #E3E3E3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 1rem 2rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
}
.wc_variaciones__item:hover {
  cursor: pointer;
  background-color: #1D57C5;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 600;
}
.wc_variaciones__nombre {
  flex-grow: 1;
}
.wc_variaciones__precio {
  font-size: 1.5rem;
  font-weight: 600;
}
.wc_variaciones__precio span.price {
  all: unset !important;
}
.wc_variaciones__detalle {
  text-align: center;
}
.wc_variaciones__add-to-cart {
  background-color: #AC1112;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
  padding: 1rem 3rem;
}
@media (min-width: 768px) {
  .wc_variaciones__add-to-cart {
    width: auto;
  }
}
.wc_variaciones__add-to-cart:hover {
  background-color: #7e0c0d;
  color: #FFFFFF;
  cursor: pointer;
}
.wc_variaciones__add-to-cart i {
  font-size: 2rem;
}
.wc_variaciones__activo {
  background-color: #1D57C5;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 600;
}

.wc_notificacion {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 3rem;
}
.wc_notificacion--activo {
  background-color: #005c55;
  color: #FFFFFF;
}
.wc_notificacion__icono {
  font-size: 2rem !important;
}
.wc_notificacion__texto {
  color: #FFFFFF;
}

.wc_guiadecompra {
  display: flex;
  justify-content: center;
  font-weight: bold;
  background-color: #f6d88a;
  border-radius: 0.5rem;
  text-align: center;
  padding: 0.5rem;
}

.wc_membresia {
  padding: 2rem 1rem;
  border-radius: 1rem;
  font-size: 1.4rem;
  text-align: left;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 3rem;
}
.wc_membresia p {
  line-height: 1.2;
}
.wc_membresia i {
  font-size: 3rem;
  color: #FFFFFF;
  padding: 1rem;
}
.wc_membresia__texto {
  margin: 0 !important;
  color: #FFFFFF;
  font-weight: bold;
}
.wc_membresia__texto--disponible {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  align-items: center;
  margin-top: 1rem !important;
  font-size: 1.8rem;
}
.wc_membresia__texto--disponible span {
  font-size: 3rem;
}
.wc_membresia--limete, .wc_membresia--expirado {
  background-color: #e92022;
}
.wc_membresia--activo {
  background-color: #005c55;
}
.wc_membresia--vacio {
  background-color: #002e2b;
}
.wc_membresia--aviso {
  background-color: #ecb014;
}

.wc_acordion__header {
  font-size: 2.2rem;
  color: #1D57C5;
  font-weight: 500;
  margin-top: 3rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  border-bottom: 1px solid #10306c;
  border-left: 5px solid #10306c;
  border-bottom-left-radius: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.wc_acordion__header:hover {
  cursor: pointer;
}
.wc_acordion__body {
  display: none;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.wc_acordion__open .wc_acordion__body {
  display: block;
}
.wc_acordion__open .wc_acordion__header {
  color: #00615A;
  color: #00615A;
  border-bottom: 1px solid black;
  border-left: 5px solid black;
  font-weight: 600;
}
.wc_acordion__open .wc_acordion__icono {
  rotate: 45deg;
}

.wc_temario ul {
  margin: 0;
  padding: 0;
}
.wc_temario ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  padding: 1rem;
  background-color: #f0f0f0;
  line-height: 1.5;
  font-weight: 500;
  color: #000000;
  margin-bottom: 0.5rem;
  border-radius: 0.5rem;
}
.wc_temario ul li::before {
  content: "►";
  font-size: 2rem;
  color: #00615A;
  font-size: 1.5rem;
  margin-right: 1rem;
}
.wc_temario ul li:nth-child(even) {
  background-color: #dedede;
}

.wc_dirigido ul {
  margin: 0;
}
.wc_dirigido ul li {
  color: #000000;
  line-height: 1.5;
  margin-bottom: 0.5rem;
}
.wc_dirigido ul li::marker {
  color: #00615A;
}

.wc_objetivos ul {
  margin: 0;
}
.wc_objetivos ul li {
  color: #000000;
  margin-bottom: 0.5rem;
  line-height: 1.5;
}
.wc_objetivos ul li::marker {
  color: #00615A;
}

.wc_docentes {
  margin-top: 3rem;
}
.wc_docentes__docente {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .wc_docentes__docente {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .wc_docentes__docente {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.wc_docentes__docente ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}
.wc_docentes__item {
  border: 1px solid #cacaca;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0.5rem;
  border-radius: 2rem;
  text-align: center;
}
.wc_docentes__imagen {
  width: 10rem;
  border-radius: 50%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.wc_docentes__label {
  color: #00615A;
  margin: 1rem 0;
  border-bottom: 1px solid #00615A;
}

.wc_calendario {
  display: flex;
  flex-direction: column;
}
.wc_calendario__link {
  margin-bottom: 2rem;
}
.wc_calendario__imagen {
  width: 100%;
}

.buscar {
  width: 100%;
  margin-bottom: 5rem;
  padding: 2rem;
}
.buscar input {
  border: 1px solid #1D57C5 !important;
  color: #1D57C5 !important;
  font-weight: bold !important;
  border-radius: 1rem !important;
}
.buscar input::placeholder {
  color: #1D57C5 !important;
  font-weight: bold !important;
}

.wc_dashboard__user {
  text-align: center;
  font-size: 2.2rem;
  color: #00615A;
  margin-bottom: 2rem;
}
.wc_dashboard__user span {
  font-weight: bold;
  color: #002e2b;
}
.wc_dashboard__titulo {
  text-align: center;
  color: #1D57C5;
  margin-bottom: 2rem;
  font-size: 2rem;
}
.wc_dashboard__grid {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .wc_dashboard__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.wc_dashboard__descripcion {
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wc_dashboard__pagar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}
.wc_dashboard__add-to-cart {
  background-color: #1D57C5;
  padding: 1.5rem 4rem;
  color: #FFFFFF !important;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
  padding: 1rem 3rem;
}
@media (min-width: 768px) {
  .wc_dashboard__add-to-cart {
    width: auto;
  }
}
.wc_dashboard__add-to-cart:hover {
  background-color: #164399;
  color: #FFFFFF;
  cursor: pointer;
}

.wcm_shop {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .wcm_shop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .wcm_shop--grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.wcm_shop__cart {
  padding: 2rem;
  display: flex;
  align-items: center;
  border-radius: 1rem;
  transition: transform 0.3s ease;
}
.wcm_shop__cart--regular {
  background-color: #E3E3E3;
}
.wcm_shop__cart--clasico {
  background-color: #b0e0f4;
}
.wcm_shop__cart--premium {
  background-color: #f9e8ba;
}
.wcm_shop__icono {
  font-size: 6rem !important;
  margin-right: 1.5rem;
  color: #979797;
}
.wcm_shop__texto {
  font-size: 1.1rem;
  color: #000000;
  margin: 0 !important;
  line-height: 2;
}
.wcm_shop__texto--precio {
  all: unset;
}
.wcm_shop__texto--precio span.price {
  color: transparent !important;
}
.wcm_shop__texto--precio span {
  font-weight: bold;
  color: #000000;
  font-size: 2.4rem;
}
.wcm_shop__texto--precio del span {
  font-size: 1.5rem;
  font-weight: 400;
}
.wcm_shop__texto span {
  font-weight: bold;
  color: #000000;
}
.wcm_shop__name {
  font-weight: bold;
  color: #000000;
}
.wcm_shop__activo.wcm_shop__cart--regular {
  background-color: #bdbdbd;
  transform: scale(1.05);
}
.wcm_shop__activo.wcm_shop__cart--clasico {
  background-color: #29ABE2;
  transform: scale(1.1);
}
.wcm_shop__activo.wcm_shop__cart--premium {
  background-color: #EEB82C;
  transform: scale(1.1);
}
.wcm_shop__activo .wcm_shop__name {
  text-transform: uppercase;
}
.wcm_shop__activo .wcm_shop__icono {
  color: #FFFFFF;
}